export default {
	data() {
		return {
			SessionKey: '',
			OpenId: '',
			nickName: null,
			avatarUrl: null,
			isCanUse: uni.getStorageSync('isCanUse')
		};
	},
	methods: {
		//第一授权获取用户信息===》按钮触发
		wxGetUserInfo() {
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					_this.nickName = infoRes.userInfo.nickName; //昵称
					_this.avatarUrl = infoRes.userInfo.avatarUrl; //头像
					try {
						uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
						_this.isCanUse = false;
						_this.updateUserInfo();
					} catch (e) {
						console.log(e)
					}
				},
				fail(res) {}
			});
		},
		//登录
		login() {
			let _this = this;
			uni.showLoading({
				title: '登录中...'
			});

			// 1.wx获取登录用户code
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					let code = loginRes.code;
					if (!_this.isCanUse) {
						//非第一次授权获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								//获取用户信息后向调用信息更新方法
								_this.nickName = infoRes.userInfo.nickName; //昵称
								_this.avatarUrl = infoRes.userInfo.avatarUrl; //头像
								uni.hideLoading();
								uni.switchTab({
									url: "/pages/index/index"
								})
							}
						});
					}
				},
			});
		},

		updateUserInfo() {
			let _this = this;
			uni.request({
				url: `${_this.serverUrl}/user/registOrLogin`, //服务器端地址
				data: {
					nickName: _this.nickName,
					headUrl: _this.avatarUrl
				},
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					if (res.data.code == 0) {
						uni.reLaunch({ //信息更新成功后跳转到小程序首页
							url: '/pages/index/index'
						});
					}
				}

			});
		}
	},
	onLoad() { //默认加载

		// if (!this.isCanUse) {
		// 	this.login();
		// }

	}
}
